@use '~sass-rem' as srem;
@use '~sass-mq/mq' as *;

@import '@styles/variables';

.root {
  // display: flex;
  // justify-content: space-between;
  // align-items: center;
  padding: srem.convert(16px);
  z-index: 100;

  // @include mq($from: tablet) {
  //   justify-content: flex-end;
  //   padding: 0;
  // }

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    'total add-to-basket'
    'reset-btn .';
  gap: 1rem;

  @include mq($from: tablet) {
    grid-template-areas: 'reset-btn . total add-to-basket';
    grid-template-columns: min-content 1fr min-content min-content;
  }
}

.total {
  font-family: $displayFont;
  font-weight: 900;
  font-size: srem.convert(20px);
  margin-right: srem.convert(40px);
  display: flex;
  align-items: center;
  grid-area: total;
}

.price {
  color: $red;
}

.resetBtn {
  grid-area: reset-btn;
  width: min-content;
}

.addToBasket {
  grid-area: add-to-basket;
}
