.canvasClickableCover {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  cursor: pointer;

  &__imgContainer {
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }
}

.navTextSceneFullscreen {
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: scroll;

  &__imgContainer {
    display: flex;
    justify-content: flex-start;
    width: 100%;
  }

  // Make this a global CSS / get a close icon
  &__closeBtn {
    font-size: 16px;
    background: black;
    color: white;
    position: absolute;
    right: 1rem;
    top: 1rem;
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    border: 3px solid black;
    font-weight: 700;
    cursor: pointer;
  }

  &__zoomContainer {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 2rem;
    display: grid;
    gap: 1rem;

    img {
      cursor: pointer;
    }
  }
}
